/*Global Var*/
:root{
    --main-red: rgba(211,9,19);
    --main-red-hover: #e50914;
    --main-dark: rgba(20,20,20);
    --main-deep-dark: rgba(0,0,0);
    --main-grey: rgba(153,153,153);
    
}

html{
    height: 100%;
}

*{
    box-sizing: border-box;
}

body{
    font-family:'Helvetica';
    -webkit-font-smoothing: antialiased;
    background: var(--main-dark);
    color:#fff; 
    overflow-x: hidden;
}

a{
    text-decoration: none;
    color: #fff;
}

/*Header Section */
.header-container {


    position:relative;
    background: radial-gradient( rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.7)),
    url('../images/bg.jpg') no-repeat center fixed;
    background-size: cover;
    height: auto;
    width: auto;
}

.header-container::after{
content:'';
position: absolute;
top: 0;
left: 100%;
height: 100%;
background: rgba(0,0,0, 0.5);
box-shadow: inset 4.375rem 3.125rem 11.875 #000, inset -4.375rem -3.125rem -11.875 #000;
}

/**************************************************************
Login Page 
***********************************/

.main-login-container{
    position:relative;
    background: radial-gradient( rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.7)),
    url('../images/bg.jpg') no-repeat center fixed;
    background-size: cover;
    height: auto;
    width: auto;
}
@media(max-width: 640px) {
    .main-login-container{
        background: #000;
    }
}

.main-login-container::after{
    width: 100%;
    height: 9100%;
    position: relative;
    background: radial-gradient(rgba(0,0,0,0), rgba(0,0,0,0.7)),
    url('../images/bg.jpg') no repeat center center/cover;
    }

.main-login-container::after{
        content:'';
        position: absolute;
        top: 0;
        left: 100%;
        height: 100%;
        background: rgba(0,0,0, 0.5);
        box-shadow: inset 70px 50px 190px rgb(58, 43, 43), 
        inset -70px -50px -190px #000;
        }

.login-header-top{
    position: relative;
    height: 10rem;
    z-index: 1
}